<template>
<div class="Url-itemPage">
    <i-modal v-model="addUserDictWindowShow" title="添加用户词" :closable="false" :mask-closable="false" :scrollable="true">

        <div class="Url-itemPage-form">

            <i-form ref="formCustom" :model="formCustom" :rules="ruleCustom" :label-width="100">

                <i-formItem label="用户词" prop="word">
                    <i-input class="management-newProject-name-input" type="text" v-model="formCustom.word" placeholder="请输入用户词"></i-input>
                </i-formItem>

                <div class="Url-itemPage-form-category">
                    <i-formItem label="分类" prop="categorySelect">
                        <div class="Url-itemPage-form-category-inside">
                            <i-select class="Url-itemPage-form-category-select" multiple v-model="formCustom.categorySelect">
                                <i-option v-for="item in rawCategories" :value="item" :key="item">{{ item }}</i-option>
                            </i-select>
                        </div>
                    </i-formItem>
                </div>
            </i-form>

        </div>
        <div slot="footer">
            <i-button type="default" size="large" @click="modalCancel()">取消</i-button>
            <i-button type="primary" size="large" @click="modalOk('formCustom')">提交</i-button>
        </div>
    </i-modal>
</div>
</template>

<script>
import {
    mapState,
    mapMutations
} from 'vuex'
export default {
    name: 'adduserDictWindow',
    data() {
        const validateUserWord = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('用户词不能为空'));

            } else {
                callback();
            }
        };
        return {
            formCustom: {
                word: '',
                categorySelect: [],
            },
            ruleCustom: {
                word: [{
                    validator: validateUserWord,
                    trigger: 'blur'
                }]
            },
        }
    },
    props: ['addUserDictWindowShow', 'rawCategories'],
    computed: {
        ...mapState(['baseurl', 'currentComponent']),
    },
    beforeDestroy() {},
    mounted() {},
    methods: {
        ...mapMutations(['changeUrlItemWindowShow']),
        modalOk: function (name) {
            let self = this
            // check 项目 是否为空
            self.$refs[name].validate((valid) => {
                if (valid) {
                    console.log('验证成功')
                    // 构造最终结果集
                    let userDictInfo = {}
                    userDictInfo['word'] = self.formCustom.word
                    userDictInfo['categories'] = self.formCustom.categorySelect
                    userDictInfo['operator'] = localStorage.getItem('kwmUser')
                    // console.log(userDictInfo)

                    // 发送创建
                    self.axios({
                            method: 'post',
                            url: self.baseurl + 'UserDict/' + self.currentComponent,
                            data: [userDictInfo]
                        })
                        .then(res => {
                            console.log(res)
                            self.$Message.success('增加用户词成功!');
                            self.$refs[name].resetFields()
                            this.$emit('update:addUserDictWindowShow', false)
                        })
                        .catch(err => {
                            console.log(err)
                            self.$Message.error('增加用户词失败!');
                        })

                } else {
                    self.$Message.error('提交失败');
                }
            })
        },
        modalCancel: function () {
            this.$emit('update:addUserDictWindowShow', false)

        }
    }
}
</script>

