<template>
<div class="Article-see-detail">
    <i-modal v-model="showSeeDetail" :title="'查看详情:  第' + xcurrentPage +  '页，第' + (xindex + 1 )  + '个'" width="750px" :closable="false" :mask-closable="false" :scrollable="true">
        <div class="Article-see-detail-content-total" v-if="showSeeDetail && articleDetail !==[]">
            <div class="Article-see-detail-content-total1">
                <div class="article-detail-content-p1">
                    {{seeBody}}
                </div>
                <div class="article-detail-content-p2">
                    <li class="liLine header">
                        <div class="p1">词语</div>
                        <div class="p2">操作</div>
                    </li>
                    <li class="liLine main" v-for="(item,index) in this.seeSplitWords" :key="index">
                        <div class="p1">{{item}}</div>
                        <div class="p2">
                            <i-button class="Stop" style="color: #fff;background-color: #54616f" size="small" @click="setStopWord(item,index)">设为停止词</i-button>
                            <i-button class="Invalid" style="color: #fff;background-color: #7b5ae4" size="small" @click="setInvalidWord(item,index)">设为无效词</i-button>
                        </div>
                    </li>
                </div>
            </div>
            <div class="Article-see-detail-content-total2">
                <i-button class="xbutton setInvalid" :disabled="this.seeStatus === '无效' || seeSplitWords.length === 0" type="warning" size="default" @click="setUrlInvalid('invalid')" icon="md-warning">设为无效</i-button>
                <i-button class="xbutton setValid" :disabled="this.seeStatus !== '无效' || seeSplitWords.length === 0" type="warning" size="default" @click="setUrlInvalid('valid')" icon="md-medkit">设为有效</i-button>
                <i-button class="xbutton addUserDict" type="success" size="default" @click="add2UserDict()">增加用户词</i-button>
            </div>
        </div>
        <div slot="footer">
            <i-button type="primary" :disabled="previousDisabled" size="large" shape="circle" @click="PreviousOne()">上一个</i-button>
            <i-button type="primary" :disabled="nextDisabled" size="large" shape="circle" @click="nextOne()">下一个</i-button>
            <i-button type="default" size="large" @click="modalCancel()">关闭</i-button>
        </div>
    </i-modal>
    <adduserDictWindow :rawCategories="rawCategories" :addUserDictWindowShow.sync="addUserDictWindowShow"></adduserDictWindow>
</div>
</template>

<script>
import {
    mapState
} from 'vuex'
import adduserDictWindow from '@/components/Articles/adduserDictWindow.vue'
export default {
    name: 'seeDetails',
    data() {
        return {
            xindex: JSON.parse(JSON.stringify(this.currentIndex)),
            xcurrentPage: JSON.parse(JSON.stringify(this.currentPage)),
            xpageSize: JSON.parse(JSON.stringify(this.pageSize)),
            xtotalItems: 0,
            articleDetail: [],
            nextDisabled: false,
            previousDisabled: false,
            addUserDictWindowShow: false,
        }
    },
    components: {
        adduserDictWindow
    },
    props: ['showSeeDetail', 'currentIndex', 'currentPage', 'pageSize', 'rawCategories'],
    computed: {
        ...mapState(['baseurl', 'currentComponent']),
        xtotalPages: function () {
            if (this.articleDetail.length !== 0) {
                return Math.ceil(this.xtotalItems / this.xpageSize)
            } else {
                return 0
            }
        },
        seeStatus: function () {
            if (this.articleDetail.length !== 0 && this.xindex < this.currentPagetotalCount && this.xindex >= 0) {
                return this.articleDetail[this.xindex].status
            } else {
                return ''
            }
        },
        seeBody: function () {
            if (this.articleDetail.length !== 0 && this.xindex < this.currentPagetotalCount && this.xindex >= 0) {
                return this.articleDetail[this.xindex].body
            } else {
                return ''
            }
        },
        seeSplitWords: function () {
            if (this.articleDetail.length !== 0 && this.xindex < this.currentPagetotalCount && this.xindex >= 0) {
                return this.articleDetail[this.xindex].splitWords
            } else {
                return []
            }
        },
        seeUid: function () {
            if (this.articleDetail.length !== 0 && this.xindex < this.currentPagetotalCount && this.xindex >= 0) {
                return this.articleDetail[this.xindex]['_id']['$oid']
            } else {
                return ''
            }
        },
        currentPagetotalCount: function () {
            return this.articleDetail.length
        }

    },
    mounted() {
        // console.log('mounted .....')
        let self = this
        if (self.showSeeDetail) {
            let Params = {
                'currentPage': self.xcurrentPage,
                'pageSize': self.xpageSize
            }
            self.fetchItem(Params)

        }
    },
    methods: {
        fetchItem: function (Params) {
            let self = this
            self.axios({
                    method: 'get',
                    url: self.baseurl + 'Articles/' + self.currentComponent,
                    params: Params
                })
                .then(res => {
                    // console.log(res)
                    if (res.data.count !== '') {
                        self.xtotalItems = res.data.count
                    }
                    self.articleDetail = res.data.content
                    // 如果点开的是 所有数据的第一个 或 最后一个， 直接禁用掉 相应的按钮
                    if (self.xindex === self.currentPagetotalCount - 1 && self.xcurrentPage === self.xtotalPages) {
                        self.nextDisabled = true
                    }
                    if (self.xindex === 0 && self.xcurrentPage === 1) {
                        self.previousDisabled = true
                    }

                })
                .catch(err => {
                    console.log(err)
                    // self.$Message.error(err.response.data.detail);
                })
        },
        add2UserDict: function () {
            let self = this
            //  往用户词中添加 词语
            // 构造 formCustom
            self.addUserDictWindowShow = true

        },
        setUrlInvalid: function (flag) {
            // 把当前 url 所在行 uid 发送到 后端 ，将该行 的 status 设置为 无效
            let self = this
            // 发送数据
            let myData = {}
            let operation = ''
            if (flag === 'invalid') {
                myData = {
                    'status': '无效'
                }
                operation = "无效"
            } else if (flag === 'valid') {
                myData = {
                    'status': '已添加'
                }
                operation = "有效"
            }

            let Params = {
                'currentPage': self.xcurrentPage,
                'pageSize': self.xpageSize
            }
            // console.log(myData)
            self.axios({
                    method: 'patch',
                    url: self.baseurl + 'Articles/' + self.currentComponent + '/' + self.seeUid,
                    data: myData,
                    params: Params
                })
                .then(res => {
                    if (res.data.count !== '') {
                        self.xtotalItems = res.data.count
                    }
                    self.articleDetail = res.data.content
                    self.$Message.success('设为' + operation + 'url成功!');
                })
                .catch(err => {
                    console.log(err)
                    self.$Message.error('设为' + operation + 'url失败!');
                })

        },
        PreviousOne: function () {
            let self = this
            // console.log(self.seeUid)
            self.nextDisabled = false
            self.xindex = self.xindex - 1
            if (self.xindex < 0) {
                // 已经到达当前第一行数据
                // 1- 页码切换到上一页:
                if (self.xcurrentPage > 1) {
                    // 可以继续进行
                    self.xindex = self.xpageSize - 1 //从上一页 最后一个 开始显示
                    self.xcurrentPage = self.xcurrentPage - 1
                    let Params = {
                        'currentPage': self.xcurrentPage,
                        'pageSize': self.xpageSize
                    }
                    self.fetchItem(Params)
                } else {
                    // 否则， 已经遍历完成。按钮设成禁用状态
                    self.previousDisabled = true
                }
            } else {
                // self.xindex = self.xindex + 1
                if (self.xindex === 0 && self.xcurrentPage === 1) {
                    self.previousDisabled = true
                }
            }
        },
        nextOne: function () {
            let self = this
            // console.log(self.seeUid)
            self.previousDisabled = false
            self.xindex = self.xindex + 1
            if (self.xindex === self.currentPagetotalCount) {
                // 已经到达当前页面数据 最低端，需要拉取新的数据
                // 1- 页码切换到下一页:
                if (self.xcurrentPage < self.xtotalPages) {
                    // 可以继续进行
                    self.xindex = 0 //从下一页第一个开始显示
                    self.xcurrentPage = self.xcurrentPage + 1
                    let Params = {
                        'currentPage': self.xcurrentPage,
                        'pageSize': self.xpageSize
                    }
                    self.fetchItem(Params)
                } else {
                    // 否则， 已经遍历完成。按钮设成禁用状态
                    self.nextDisabled = true
                }
            } else {
                // self.xindex = self.xindex + 1
                if (self.xindex === self.currentPagetotalCount - 1 && self.xcurrentPage === self.xtotalPages) {
                    self.nextDisabled = true
                }
            }
        },
        modalCancel: function () {
            //this.showSeeUrlConfig = false
            this.$emit('forceRefresh', this.xcurrentPage)
            this.$emit('update:showSeeDetail', false)

        },
        // 设为停止词
        setStopWord: function (word, index) {
            let self = this
            // console.log(word,index)
            // 1- 首先将该词 添加到 停止词 词典
            let myData = [{
                'word': word,
                'operator': localStorage.getItem('kwmUser')
            }]
            // console.log(myData)
            self.axios({
                    method: 'post',
                    url: self.baseurl + 'StopDict/' + self.currentComponent,
                    data: myData
                })
                .then(() => {
                    //console.log(res)
                    // 2- 将 该词 从 原始记录中删除
                    let temp = JSON.parse(JSON.stringify(self.seeSplitWords))
                    temp.splice(index, 1)
                    let myData = {
                        'splitWords': temp
                    }
                    let Params = {
                        'currentPage': self.xcurrentPage,
                        'pageSize': self.xpageSize
                    }
                    console.log('uid', self.seeUid)
                    // 更新 Article 后端，从 article 数据库中，删除 停止词
                    self.axios({
                            method: 'patch',
                            url: self.baseurl + 'Articles/' + self.currentComponent + '/' + self.seeUid,
                            data: myData,
                            params: Params
                        })
                        .then(res => {
                            // console.log('yyy',res)
                            //self.seeSplitWords.splice(index,1)
                            if (res.data.count !== '') {
                                self.xtotalItems = res.data.count
                            }
                            self.articleDetail = res.data.content
                            self.$Message.success('设为停止词成功!');
                        })
                        .catch(err => {
                            console.log(err)

                            // 如果失败，那么 将词 从 停止词典中删除
                            self.$Message.error('设置失败,正在回滚 ...');
                            let myData = [word]
                            self.axios({
                                    method: 'delete',
                                    url: self.baseurl + 'StopDict/' + self.currentComponent,
                                    data: myData
                                })
                                .then(res => {
                                    console.log(res)
                                })
                                .catch(err => {
                                    console.log(err)
                                })
                        })
                })
                .catch(err => {
                    console.log(err)
                })
        },
        // 设为无效词
        setInvalidWord: function (word, index) {
            let self = this
            // console.log(word,index)
            // 1- 首先将该词 添加到 无效词 词典
            let myData = [{
                'word': word,
                'operator': localStorage.getItem('kwmUser')
            }]
            // console.log(myData)
            self.axios({
                    method: 'post',
                    url: self.baseurl + 'InvalidDict/' + self.currentComponent,
                    data: myData
                })
                .then(() => {
                    //console.log(res)
                    // 2- 将 该词 从 原始记录中删除
                    let temp = JSON.parse(JSON.stringify(self.seeSplitWords))
                    temp.splice(index, 1)
                    let myData = {
                        'splitWords': temp
                    }
                    let Params = {
                        'currentPage': self.xcurrentPage,
                        'pageSize': self.xpageSize
                    }
                    console.log('uid', self.seeUid)
                    // 更新 Article 后端，从 article 数据库中，删除 无效词
                    self.axios({
                            method: 'patch',
                            url: self.baseurl + 'Articles/' + self.currentComponent + '/' + self.seeUid,
                            data: myData,
                            params: Params
                        })
                        .then(res => {
                            // console.log('yyy',res)
                            //self.seeSplitWords.splice(index,1)
                            if (res.data.count !== '') {
                                self.xtotalItems = res.data.count
                            }
                            self.articleDetail = res.data.content
                            self.$Message.success('设为无效词成功!');
                        })
                        .catch(err => {
                            console.log(err)

                            // 如果失败，那么 将词 从 停止词典中删除
                            self.$Message.error('设置失败,正在回滚 ...');
                            let myData = [word]
                            self.axios({
                                    method: 'delete',
                                    url: self.baseurl + 'InvalidDict/' + self.currentComponent,
                                    data: myData
                                })
                                .then(res => {
                                    console.log(res)
                                })
                                .catch(err => {
                                    console.log(err)
                                })
                        })
                })
                .catch(err => {
                    console.log(err)
                })
        },
    }
}
</script>

<style scoped>
