    list-style-type: none;
    padding: 0px !important
}

.xbutton {
    margin-left: 10px;
    margin-bottom: 10px
}

.liLine {
    display: flex;
    align-items: center;
    padding: 3px;
}

.liLine.header {
    border-bottom: 1px solid red;
